import React from 'react';

import './styles.css';

function SwappersItem({ swappers }) {
  return (
    <li key={swappers.data.idUsuario} className="swapper-item">
      <header>
        <img src="data:image/jpeg;base64," alt="imagem"></img>
        <div className="user-info">
          <strong>{swappers.data.nome}</strong>
          <span>{swappers.data.dispositivos.join(' | ')}</span>
        </div>
      </header>
      <p>teste aplicação swapped!</p>
      <a href="www.google.com">bla blabla</a>
    </li>
  );
}

export default SwappersItem;